import React, { useContext, Fragment } from 'react';
import { me } from '../../apis/auth';
import AppContext from '../../AppContext';
export default () => {
  const {
    data: { loggedInUser = {} },
  } = useContext(AppContext);
  return (
    <div>
      Welcome {loggedInUser.username}
      <div>
        <ul>
          {loggedInUser.username ? (
            <a href="/connect">
              <li>Call</li>
            </a>
          ) : (
            <Fragment>
              <a href="/login">
                <li>Login</li>
              </a>
              <a href="/register">
                <li>Register</li>
              </a>
            </Fragment>
          )}
        </ul>
      </div>
    </div>
  );
};
